import React, {Component} from 'react';
import 'antd/dist/antd.css';
import { Button, Checkbox, Form, Input } from 'antd';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import {Link} from "react-router-dom";
class SignIn extends Component {
    constructor(props) {
        super(props);
        this.state={
            username:'',
            password:''
        }
    }
    onSignIn = () => {
        const error =() => toast.error('Wrong')
        const sign =() => toast(<Button>
                <Link to="/home">Sign in</Link>
        </Button>
        );
        if(localStorage.getItem('username')==='abc1' && localStorage.getItem('password')==='abc1'){ sign()}
        else {error() }
    };
    onFinish = (values) => {
        localStorage.setItem('username',values.username)
        localStorage.setItem('password',values.password)
        console.log('Success:', values);
        console.log('abc',this.props.account);
        this.onSignIn();
    };
    onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };
    render() {
        const {username,password} =this.state
        return (
            <Form
                name="basic"
                labelCol={{
                    span: 8,
                }}
                wrapperCol={{
                    span: 16,
                }}
                initialValues={{
                    remember: true,
                }}
                onFinish={this.onFinish}
                onFinishFailed={this.onFinishFailed}
                autoComplete="off"
            >
                <Form.Item
                    label="Username"
                    name="username"
                    value={username}
                    rules={[
                        {
                            required: true,
                            message: 'Please input your username!',

                        },
                    ]}
                >
                    <Input/>
                </Form.Item>

                <Form.Item
                    label="Password"
                    name='password'
                    value={password}
                    rules={[
                        {
                            required: true,
                            message: 'Please input your password!',
                        },
                    ]}
                >
                    <Input.Password/>
                </Form.Item>

                <Form.Item
                    name="remember"
                    valuePropName="checked"
                    wrapperCol={{
                        offset: 8,
                        span: 16,
                    }}
                >
                    <Checkbox>Remember me</Checkbox>
                </Form.Item>

                <Form.Item
                    wrapperCol={{
                        offset: 8,
                        span: 16,
                    }}
                >
                    <Button type="primary" htmlType="submit" onClick={() => console.log(this.props.account)}>
                        Submit
                    </Button>
                </Form.Item>
                <ToastContainer />
            </Form>
        );
    }
}
export default SignIn;